.App {
  text-align: center;
  background-color: #f0f2f5;
  min-height: 100vh;
}

.App-header {
  background-color: #282c34;
  padding: 40px 20px;
  color: white;
}

.App-header h1 {
  font-size: 3rem;
  margin: 0;
}

.App-header p {
  font-size: 1.2rem;
  color: #b0b3b8;
}

.App-main {
  padding: 20px;
}

.artists-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.artist-card {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  text-align: left;
  transition: transform 0.2s;
}

.artist-card:hover {
  transform: translateY(-5px);
}

.artist-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.artist-info {
  padding: 20px;
}

.artist-name {
  font-size: 1.5rem;
  margin: 0 0 10px 0;
  color: #333;
}

.artist-bio {
  font-size: 1rem;
  color: #666;
  line-height: 1.5;
}

.error-message {
  color: #d32f2f;
  font-weight: bold;
}
